	module.exports = {"data":{"post":{"html":"<p>Yet another <a title=\"Europe pines for big-spending US tourists | csmonitor.com\" href=\"http://www.csmonitor.com/2003/0818/p07s01-woeu.html\">'where have all the US tourists gone'</a> article noted by <a title=\"Instapundit.com:\" href=\"http://www.instapundit.com/archives/011030.php\">Instapundit</a>.  Funny how these articles never mention Germany. I've seen statistics that the number of US visitors is down, but haven't read anyone complaining. The German tourist industry industry seems more concerned with keeping German tourists at home. </p>\n<p>Even though Hamburg is a big tourist town, I've never noticed very many Americans among the foreign accents. Maybe it's because the main attractions are theater and sex (er, I mean, night life), both in the German language.</p>","fields":{"title":"Night life","date":"18 Aug 2003","url":"https://papascott.de/2003-08-18-night-life/"}}},"pathContext":{"slug":"/2003-08-18-night-life/","prev":{"fields":{"title":"TAZ: 5-times more wiretaps per capita in Germany as in USA","date":"17 Aug 2003","slug":"/2003-08-17-taz-5-times-more-wiretaps-per-capita-in-germany-as-in-usa/"}},"next":{"fields":{"title":"Study: Which country is least likey to suffer a terrorist attack","date":"18 Aug 2003","slug":"/2003-08-18-study-which-country-is-least-likey-to-suffer-a-terrorist-attack/"}}}}

